<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('LC_TYPE') }}:</span>
          <span>{{ bill.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LC_NUMBER') }}:</span>
          <span>{{ bill.lcNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ADDITIONAL_REF') }}:</span>
          <span>{{ bill.billNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('BENEFICIARY_NAME') }}:</span>
          <span>{{ bill.beneficiaryName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ bill.currencyAbbreviation | placeholder('-') }}</span>
        </p>
      </div>
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('AMOUNT') }}:</span>
          <span>{{ bill.billAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('PAYMENT_DUE') }}:</span>
          <span>{{ bill.dueDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TENOR_DETAILS') }}:</span>
          <span>{{ bill.paymentTerms | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('STATUS_OF_DOCUMENTS') }}:</span>
          <span>{{ bill.auxiliary1 | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DATE_ADVISED') }}:</span>
          <span>{{ bill.adviseDate | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import TradeFinanceService from '@/services/trade/TradeFinanceService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'

export default {
  name: 'export-lc-bill-sent-view',
  mixins: [LoadingMixin, AlertMixin],
  data: function () {
    return {
      bill: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      TradeFinanceService.sentExportLcBills({
        lcNumber: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.bill = response.data.billList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
